input[type='button'],
input[type='submit'] {

  font-family: @font-general;
  border: 0;
  padding: 7px 13px;
  cursor: pointer;

}

.button-confirm {

  color: #fff;
  background-color: @darkPrimaryColor;
  font-weight: 600;

}

.button-warning {

  color: #fff;
  background-color: @primaryTextColor;
  font-weight: 600;

}

.button-neutral {

  color: #333;
  background-color: @dividerColor;
  font-weight: 600;

}