.register {
  line-height: 16px;
  float: right;
}

.register,
.login {
  width: 250px;
  padding-left: 25px;  
  padding-right: 25px;  
}

.login:after {
  content: 'oder';
  position: absolute;
  right: -9px;
  top: 50%;
  background-color: #fff;
  margin-top: -7px;
  text-transform: uppercase;
  color: rgba(0,0,0,0.5);
  font-weight: 600;
  font-size: 14px;
}

.login {
  float: left;
  text-align: left;
  border-right: 1px rgba(0,0,0,0.5) solid;
  position: relative;
}

.login a {
  color: #3b5998;
  padding: 8px 18px 5px 18px;
}

.login input[type=submit] {
  text-transform: uppercase;
  color: #fff;
  padding: 8px 18px 5px 18px;
  font-size: 16px;
}