header {
  background-color: @primaryTextColor;
  color: #fff;
  line-height: 50px;
  height: 50px;
}

.header-home,
.header-navigation,
.header-user {
  display: inline-block;
}

.header-home,
.header-navigation {
  float: left;
  line-height: inherit;
}

.header-home:before {
  font-family: FontAwesome;
  content: '\f015';
  float: left;
  font-size: 1.6em;
}

.header-user {
  float: right;
}

.header-topline {
  height: 200px;
  background-color: @darkPrimaryColor;
}